.victorybox{
    position: absolute;
    top:20%;
    left:20%;
    z-index: 100;
    background-color: aquamarine;
  }


  .close {
    color: #aaa;
    float: right;
    font-size: 2em;
    font-weight: bold;
  }
  
  .close:hover,
  .close:focus {
    color: black;
    text-decoration: none;
    cursor: pointer;
  } 

  .victory {
    border: 2px solid blue; 
    background-color: #107896;
    color: white;
  }